body {
  margin: 0;
  background: rgb(18, 18, 18);
  color: white;
  position: relative;
}

.container {
  width: 90%;
  max-width: 995px;
  margin: 0 auto;
}

.head {
  display: flex;
  width: 75%;
  margin: 0 auto;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  padding: 10px;
  background: rgb(18, 18, 18);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 10px;
}

.logo {
  font-size: 40px;
  font-weight: bold;
  margin-right: 10px;
}

.header input {
  width: 100%;
  outline: none;
  font-size: 20px;
  padding: 0 15px;
}


@media (max-width: 840px) {

  .head {
    width: 100%;
  }

  .header input {
    margin-right: 20px;
  }

  .container {
    width: 90%;
  }
}

